import React from "react"

export default function SingleProductPage({ data }){
	const product = data.wcProducts;
	return (
		<div>
		
		<h1>{product.name}</h1>
		 
		</div>
	)
}

export const query = graphql`
  query($slug: String!) {
  wcProducts(slug: {eq: $slug}) {
    name
    on_sale
    sku
    price_html
    price
    short_description
    type
    attributes {
      slug
      name
      options
    }
    categories {
      id
      name
      slug
      wordpress_id
    }
    description
  }
}

`;